body, h2, p {
  margin: 0;
  padding: 0;
}

body {
  background-color: blueviolet;
  padding: 3em;
}

header img {
  width: 14%;
}

main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}

main img {
  width: 50%;
}

.content {
  width: 40%;
  padding-top: 2em;
}

.content h2 {
  font-size: 45px;
  color: #fff;
}

.content p {
  margin: 2em 0em;
  font-size: 15px;
  color: #fff;
}

main button {
  padding: 1em;
  border: none;
  border-radius: 30px;
  color: #800080;
  width: 40%;
}